// components/sections/MenuSection.tsx
"use client";

import { useState } from "react";
import Image from "next/image";
import { SiteConfig, Lang, LocalizedText } from "../../lib/siteConfig";

function t(text: LocalizedText, lang: Lang) {
  return lang === "ar" ? text.ar : text.en;
}

interface MenuSectionProps {
  config: SiteConfig;
  lang: Lang;
}

export function MenuSection({ config, lang }: MenuSectionProps) {
  const [activeCategoryId, setActiveCategoryId] =
    useState<SiteConfig["menuSection"]["categories"][number]["id"]>("main");

  const activeCategory =
    config.menuSection.categories.find((c) => c.id === activeCategoryId) ??
    config.menuSection.categories[0];

  const currency = config.footer.currencySymbol;
  const isAr = lang === "ar";

  return (
    <section
      id="menu"
      className="bg-gradient-to-b from-slate-50 via-slate-50 to-slate-100/80 py-12 md:py-16"
    >
      <div className="mx-auto max-w-6xl px-3 sm:px-4">
        {/* ===== DESKTOP LAYOUT WRAPPER (image left, menu right) ===== */}
        <div className="lg:grid lg:grid-cols-[minmax(0,1.05fr)_minmax(0,1.4fr)] lg:gap-10 lg:items-start">
          {/* ===== LEFT: BIG MENU BANNER (STACKS ON TOP IN MOBILE) ===== */}
          <div className="mb-10 lg:mb-0">
            <div className="relative overflow-hidden rounded-[36px] bg-slate-900 shadow-xl">
              <div className="relative h-56 sm:h-64 md:h-72 lg:h-[420px]">
                <Image
                  src="/cafe/menu-banner.jpg"
                  alt={t(config.menuSection.title, lang)}
                  fill
                  className="object-cover"
                  sizes="(min-width: 1024px) 480px, 100vw"
                  unoptimized
                />
                {/* light gradient overlay فقط */}
                <div className="pointer-events-none absolute inset-0 bg-gradient-to-tr from-black/20 via-black/10 to-black/0" />
              </div>
            </div>
          </div>

          {/* ===== RIGHT: HEADER + TABS + MENU LIST ===== */}
          <div>
            {/* HEADER: label + title */}
            <div className="mb-6">
              <div className={isAr ? "text-right" : "text-left"}>
                <p className="mb-1 text-[11px] font-semibold uppercase tracking-[0.2em] text-red-500">
                  {t(config.menuSection.label, lang)}
                </p>
                <h2 className="text-2xl font-extrabold text-slate-900 sm:text-3xl md:text-[32px]">
                  {t(config.menuSection.title, lang)}
                </h2>
              </div>
            </div>

            {/* CATEGORY TABS */}
            <div className="mb-7">
              {/* MOBILE: 2x2 GRID (unchanged) */}
              <div className="grid max-w-md grid-cols-2 gap-3 md:hidden">
                {config.menuSection.categories.map((cat) => {
                  const isActive = cat.id === activeCategoryId;
                  return (
                    <button
                      key={cat.id}
                      onClick={() => setActiveCategoryId(cat.id)}
                      className={
                        "w-full rounded-xl border px-4 py-2 text-sm font-semibold transition " +
                        (isActive
                          ? "border-red-500 bg-red-500 text-white shadow-md"
                          : "border-slate-200 bg-white text-slate-800 hover:bg-slate-50")
                      }
                    >
                      {t(cat.label, lang)}
                    </button>
                  );
                })}
              </div>

              {/* DESKTOP/TABLET: HORIZONTAL TABS */}
              <div className="hidden md:flex md:flex-wrap md:gap-3">
                {config.menuSection.categories.map((cat) => {
                  const isActive = cat.id === activeCategoryId;
                  return (
                    <button
                      key={cat.id}
                      onClick={() => setActiveCategoryId(cat.id)}
                      className={
                        "rounded-full border px-5 py-2 text-sm font-semibold transition " +
                        (isActive
                          ? "border-red-500 bg-red-500 text-white shadow-md"
                          : "border-slate-200 bg-white text-slate-800 hover:bg-slate-50")
                      }
                    >
                      {t(cat.label, lang)}
                    </button>
                  );
                })}
              </div>
            </div>

            {/* MENU LIST WITH IMAGES */}
            <div className="space-y-4">
              {activeCategory.items.map((item) => {
                const itemImage = (item as any).image as string | undefined;

                return (
                  <div
                    key={item.id}
                    className="group rounded-2xl bg-white/95 px-3 py-3 shadow-sm ring-1 ring-slate-100 transition hover:bg-white hover:shadow-md sm:px-4 sm:py-4"
                  >
                    <div
                      className={
                        "flex items-start gap-3 sm:gap-4 " +
                        (isAr ? "flex-row-reverse" : "flex-row")
                      }
                    >
                      {/* dish image */}
                      <div className="relative h-16 w-16 shrink-0 overflow-hidden rounded-2xl bg-slate-200 sm:h-20 sm:w-20">
                        {itemImage ? (
                          <Image
                            src={itemImage}
                            alt={t(item.name, lang)}
                            fill
                            className="object-cover transition-transform duration-300 group-hover:-translate-y-0.5 group-hover:scale-105"
                            sizes="80px"
                            unoptimized
                          />
                        ) : (
                          <div className="flex h-full w-full items-center justify-center text-[11px] text-slate-500">
                            {lang === "en" ? "Dish" : "طبق"}
                          </div>
                        )}
                        <div className="pointer-events-none absolute inset-0 bg-black/0 transition group-hover:bg-black/5" />
                      </div>

                      {/* name + desc + price */}
                      <div className="flex-1">
                        <div
                          className={
                            "flex items-baseline gap-3 " +
                            (isAr ? "flex-row-reverse" : "flex-row")
                          }
                        >
                          <p className="text-base font-semibold text-slate-900 sm:text-lg">
                            {t(item.name, lang)}
                          </p>

                          <span className="mt-2 flex-1 border-b border-dashed border-slate-300" />

                          <div className="whitespace-nowrap text-right text-sm font-bold text-red-500 sm:text-base">
                            <span>
                              {currency}
                              {item.priceFull.toFixed(0)}
                            </span>
                          </div>
                        </div>

                        <p
                          className={
                            "mt-1 text-xs text-slate-500 sm:text-[13px] " +
                            (isAr ? "text-right" : "text-left")
                          }
                        >
                          {t(item.description, lang)}
                        </p>
                      </div>
                    </div>
                  </div>
                );
              })}
            </div>
          </div>
        </div>
      </div>
    </section>
  );
}
