// components/sections/HeroSection.tsx
"use client";

import Image from "next/image";
import { SiteConfig, Lang, LocalizedText } from "../../lib/siteConfig";

function t(text: LocalizedText, lang: Lang) {
  return lang === "ar" ? text.ar : text.en;
}

interface HeroSectionProps {
  config: SiteConfig;
  lang: Lang;
}

// ✅ Put your banner here
const HERO_BANNER_SRC = "/hero-bg.svg"; // or "/Untitled%20design.svg"

export function HeroSection({ config, lang }: HeroSectionProps) {
  const accent = config.primaryColor ?? "#E28A3A";
  const isAr = lang === "ar";

  const handleMenuClick = () => {
    const menuSection = document.getElementById("menu");
    if (menuSection)
      menuSection.scrollIntoView({ behavior: "smooth", block: "start" });
  };

  return (
    <section id="hero" className="relative w-full overflow-hidden">
      {/* Full banner */}
      <div className="relative w-full pt-[76px] md:pt-0">
        {/* MOBILE (show full banner – no crop) */}
        <div className="relative md:hidden h-[calc(100svh-76px)] min-h-[520px] w-full">
          <Image
            src={HERO_BANNER_SRC}
            alt={t(config.brandName, lang)}
            fill
            priority
            unoptimized
            className="object-contain"
            sizes="100vw"
          />
          {/* subtle overlay */}
          <div className="pointer-events-none absolute inset-0 bg-black/10" />

          {/* ✅ Mobile Floating CTA */}
          <div className="absolute inset-x-0 bottom-5 flex justify-center px-4">
            <button
              type="button"
              onClick={handleMenuClick}
              className="group relative inline-flex w-full max-w-[360px] items-center justify-center gap-2 rounded-full px-6 py-3 text-sm font-extrabold text-white shadow-2xl active:scale-[0.98]"
              style={{
                backgroundColor: accent,
                boxShadow: "0 18px 40px rgba(58,30,18,0.25)",
              }}
            >
              {/* glow ring */}
              <span
                className="pointer-events-none absolute -inset-1 rounded-full opacity-55 blur-md"
                style={{ backgroundColor: accent }}
              />

              {/* subtle shine */}
              <span className="pointer-events-none absolute inset-0 rounded-full bg-[linear-gradient(120deg,rgba(255,255,255,0.35),transparent_40%,transparent_60%,rgba(255,255,255,0.25))] opacity-70" />

              <span className="relative">
                {isAr ? "عرض المنيو" : "View Menu"}
              </span>

              {/* arrow */}
              <span className="relative inline-flex h-8 w-8 items-center justify-center rounded-full bg-white/18 ring-1 ring-white/25 transition-transform duration-200 group-hover:translate-x-0.5">
                <svg
                  width="16"
                  height="16"
                  viewBox="0 0 24 24"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                  className={isAr ? "rotate-180" : ""}
                >
                  <path
                    d="M9 18L15 12L9 6"
                    stroke="white"
                    strokeWidth="2.5"
                    strokeLinecap="round"
                    strokeLinejoin="round"
                  />
                </svg>
              </span>

              {/* pulse */}
              <span
                className="pointer-events-none absolute inset-0 rounded-full animate-[pulse_2.2s_ease-in-out_infinite] opacity-30"
                style={{ backgroundColor: accent }}
              />
            </button>
          </div>
        </div>

        {/* DESKTOP (fill premium) */}
        <div className="relative hidden md:block h-[70vh] lg:h-[88vh] min-h-[680px] w-full">
          <Image
            src={HERO_BANNER_SRC}
            alt={t(config.brandName, lang)}
            fill
            priority
            unoptimized
            className="object-cover"
            sizes="100vw"
          />
          <div className="pointer-events-none absolute inset-0 bg-black/10" />

          {/* ✅ Desktop CTA (elegant corner button) */}
          <div className="absolute bottom-6 right-6">
            <button
              type="button"
              onClick={handleMenuClick}
              className="group relative inline-flex items-center gap-3 rounded-full border px-5 py-3 text-sm font-extrabold backdrop-blur-md transition-transform active:scale-[0.98]"
              style={{
                borderColor: "rgba(255,255,255,0.35)",
                backgroundColor: "rgba(255,255,255,0.22)",
                color: "white",
                boxShadow: "0 18px 40px rgba(58,30,18,0.22)",
              }}
            >
              <span className="relative">
                {isAr ? "عرض المنيو" : "View Menu"}
              </span>

              <span
                className="relative inline-flex h-9 w-9 items-center justify-center rounded-full"
                style={{
                  backgroundColor: accent,
                  boxShadow: "0 12px 26px rgba(226,138,58,0.35)",
                }}
              >
                <svg
                  width="16"
                  height="16"
                  viewBox="0 0 24 24"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                  className={
                    "transition-transform duration-200 " +
                    (isAr ? "rotate-180 group-hover:-translate-x-0.5" : "group-hover:translate-x-0.5")
                  }
                >
                  <path
                    d="M9 18L15 12L9 6"
                    stroke="white"
                    strokeWidth="2.5"
                    strokeLinecap="round"
                    strokeLinejoin="round"
                  />
                </svg>
              </span>

              {/* hover glow */}
              <span
                className="pointer-events-none absolute -inset-1 rounded-full opacity-0 blur-lg transition-opacity duration-200 group-hover:opacity-45"
                style={{ backgroundColor: accent }}
              />
            </button>
          </div>
        </div>
      </div>
    </section>
  );
}
