// components/sections/QrSection.tsx
"use client";

import Image from "next/image";
import { SiteConfig, Lang, LocalizedText } from "../../lib/siteConfig";

function t(text: LocalizedText, lang: Lang) {
  return lang === "ar" ? text.ar : text.en;
}

interface QrSectionProps {
  config: SiteConfig;
  lang: Lang;
}

const galleryImages = [
  { src: "/gallery/1.jpg", alt: "Coffee cherries" },
  { src: "/gallery/2.jpg", alt: "Red poster" },
  { src: "/gallery/3.jpg", alt: "Roasting" },
  { src: "/gallery/4.jpg", alt: "Old photo" },
  { src: "/gallery/5.jpg", alt: "Cafe shot" },
  { src: "/gallery/6.jpg", alt: "Beans closeup" },
];

export function QrSection({ config, lang }: QrSectionProps) {
  const isAr = lang === "ar";

  // duplicate for seamless loop
  const rowA = [...galleryImages, ...galleryImages];
  const rowB = [...galleryImages.slice().reverse(), ...galleryImages.slice().reverse()];

  return (
    <section
      id="qr"
      className="relative w-full overflow-hidden bg-gradient-to-b from-slate-50 via-slate-50 to-slate-100/60 py-10 md:py-14"
    >
      {/* Title */}
      <div className="mx-auto max-w-6xl px-4">
        <div className={isAr ? "text-right" : "text-left"}>
          <h2 className="mb-2 text-2xl font-extrabold text-slate-900 md:text-3xl">
            {t(config.qrSection.title, lang)}
          </h2>
          <p className="mb-6 text-sm text-slate-600 md:text-base">
            {t(config.qrSection.text, lang)}
          </p>
        </div>
      </div>

      {/* Full-width moving gallery */}
      <div className="relative">
        {/* soft fade edges */}
        <div className="pointer-events-none absolute inset-y-0 left-0 z-10 w-16 bg-gradient-to-r from-slate-50 to-transparent md:w-24" />
        <div className="pointer-events-none absolute inset-y-0 right-0 z-10 w-16 bg-gradient-to-l from-slate-50 to-transparent md:w-24" />

        {/* ✅ IMPORTANT: force marquee rows to LTR always (fix RTL break) */}
        {/* Row 1 */}
        <div className="group overflow-hidden py-2" dir="ltr">
          <div className="marquee-ltr flex w-max gap-4 px-4 will-change-transform motion-reduce:animate-none">
            {rowA.map((img, idx) => (
              <div
                key={`a-${idx}-${img.src}`}
                className="relative h-52 w-[260px] shrink-0 overflow-hidden rounded-3xl border border-white/70 bg-slate-200 shadow-xl ring-1 ring-slate-100 sm:h-60 sm:w-[300px] md:h-72 md:w-[360px]"
              >
                <Image
                  src={img.src}
                  alt={img.alt}
                  fill
                  sizes="(min-width: 1024px) 360px, (min-width: 640px) 300px, 260px"
                  className="object-cover transition-transform duration-500 group-hover:scale-[1.02]"
                  unoptimized
                />
                <div className="pointer-events-none absolute inset-0 bg-gradient-to-tr from-black/20 via-black/5 to-transparent" />
              </div>
            ))}
          </div>
        </div>

        {/* Row 2 (opposite direction + slightly different sizes) */}
        <div className="group overflow-hidden py-2" dir="ltr">
          <div className="marquee-rtl-slow flex w-max gap-4 px-4 will-change-transform motion-reduce:animate-none">
            {rowB.map((img, idx) => (
              <div
                key={`b-${idx}-${img.src}`}
                className="relative h-48 w-[220px] shrink-0 overflow-hidden rounded-3xl border border-white/70 bg-slate-200 shadow-lg ring-1 ring-slate-100 sm:h-56 sm:w-[280px] md:h-64 md:w-[330px]"
              >
                <Image
                  src={img.src}
                  alt={img.alt}
                  fill
                  sizes="(min-width: 1024px) 330px, (min-width: 640px) 280px, 220px"
                  className="object-cover transition-transform duration-500 group-hover:scale-[1.02]"
                  unoptimized
                />
                <div className="pointer-events-none absolute inset-0 bg-gradient-to-tr from-black/18 via-black/6 to-transparent" />
              </div>
            ))}
          </div>
        </div>

        {/* Small note */}
        <div className="mx-auto mt-4 max-w-6xl px-4">
          <p className={"text-[11px] text-slate-500 md:text-xs " + (isAr ? "text-right" : "text-left")}>
            {t(config.qrSection.note, lang)}
          </p>
        </div>
      </div>

      {/* ✅ scoped global keyframes (no need to edit globals.css) */}
      <style jsx global>{`
        .marquee-ltr {
          animation: marquee-ltr 22s linear infinite;
        }
        .marquee-rtl-slow {
          animation: marquee-rtl 30s linear infinite;
        }

        /* pause on hover */
        .group:hover .marquee-ltr,
        .group:hover .marquee-rtl-slow {
          animation-play-state: paused;
        }

        @keyframes marquee-ltr {
          0% {
            transform: translateX(0);
          }
          100% {
            transform: translateX(-50%);
          }
        }

        @keyframes marquee-rtl {
          0% {
            transform: translateX(-50%);
          }
          100% {
            transform: translateX(0);
          }
        }
      `}</style>
    </section>
  );
}
