// components/SiteHeader.tsx
"use client";

import { useEffect, useMemo, useState } from "react";
import Image from "next/image";
import { Lang, LocalizedText, SiteConfig } from "../lib/siteConfig";

function t(text: LocalizedText, lang: Lang) {
  return lang === "ar" ? text.ar : text.en;
}

interface SiteHeaderProps {
  config: SiteConfig;
  lang: Lang;
  onChangeLang: (lang: Lang) => void;
}

export function SiteHeader({ config, lang, onChangeLang }: SiteHeaderProps) {
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [scrolled, setScrolled] = useState(false);

  const isAr = lang === "ar";

  const accent = config.primaryColor ?? "#E28A3A";

  // Chocolate palette (for glass header)
  const cocoa = "#3A1E12";
  const cocoa2 = "#6B3E2A";

  const navLinks: { id: string; label: LocalizedText }[] = [
    { id: "hero", label: { en: "Home", ar: "الرئيسية" } },
    { id: "qr", label: { en: "Gallery", ar: "صور" } },
    { id: "menu", label: { en: "Menu", ar: "المنيو" } },
    { id: "offers", label: { en: "Offers", ar: "العروض" } },
    { id: "popular", label: { en: "Popular", ar: "الأكثر طلبًا" } },
    { id: "contact", label: { en: "Contact", ar: "تواصل" } },
  ];

  useEffect(() => {
    const onScroll = () => setScrolled(window.scrollY > 10);
    onScroll();
    window.addEventListener("scroll", onScroll, { passive: true });
    return () => window.removeEventListener("scroll", onScroll);
  }, []);

  const handleChangeLang = (nextLang: Lang) => {
    if (nextLang !== lang) onChangeLang(nextLang);
  };

  const handleNavClick = () => setIsMenuOpen(false);
  const toggleMenu = () => setIsMenuOpen((prev) => !prev);

  const headerCardStyle = useMemo(() => {
    // قبل السكرول: شفاف أكثر حتى يطلع البنر
    // بعد السكرول: أوضح + ظل أقوى للقراءة
    return {
      backgroundColor: scrolled ? "rgba(255,255,255,0.82)" : "rgba(255,255,255,0.58)",
      borderColor: scrolled ? "rgba(255,255,255,0.75)" : "rgba(255,255,255,0.55)",
      boxShadow: scrolled
        ? "0 18px 45px rgba(58,30,18,0.18)"
        : "0 12px 35px rgba(58,30,18,0.14)",
    } as const;
  }, [scrolled]);

  return (
    <header className="fixed inset-x-0 top-0 z-50">
      {/* soft chocolate glow behind */}
      <div className="pointer-events-none absolute inset-x-0 top-0 h-24">
        <div
          className="absolute -left-20 -top-16 h-56 w-56 rounded-full blur-3xl opacity-25"
          style={{ backgroundColor: accent }}
        />
        <div
          className="absolute right-[-70px] -top-16 h-64 w-64 rounded-full blur-3xl opacity-18"
          style={{ backgroundColor: "#2d140b" }}
        />
        {/* subtle top fade */}
        <div className="absolute inset-x-0 top-0 h-24 bg-gradient-to-b from-white/25 to-transparent" />
      </div>

      {/* container */}
      <div className="mx-auto w-full max-w-7xl px-4 pt-4 lg:px-6">
        {/* glass card */}
        <div
          className="relative rounded-[999px] border backdrop-blur-xl"
          style={headerCardStyle}
        >
          <div className="flex items-center justify-between gap-3 px-4 py-3 md:px-5">
            {/* Logo + Brand */}
            <div className="flex items-center gap-2">
              <div
                className="relative h-10 w-10 overflow-hidden rounded-full shadow-sm ring-1 md:h-11 md:w-11"
                style={{
                  backgroundColor: "rgba(255,255,255,0.9)",
                  ringColor: "rgba(58,30,18,0.10)",
                } as any}
              >
                <Image
                  src="/logo/logo.png"
                  alt={t(config.brandName, lang)}
                  fill
                  sizes="44px"
                  className="object-cover"
                  priority
                />
              </div>

              <div className={isAr ? "text-right" : "text-left"}>
                <span
                  className="block text-base font-extrabold tracking-wide md:text-lg"
                  style={{ color: cocoa }}
                >
                  {t(config.brandName, lang)}
                </span>
              </div>
            </div>

            {/* Desktop navigation */}
            <nav className="hidden items-center gap-6 text-sm font-medium md:flex">
              {navLinks.map((item) => (
                <a
                  key={item.id}
                  href={`#${item.id}`}
                  className={`group relative transition-colors ${
                    isAr ? "text-[15px]" : ""
                  }`}
                  style={{ color: cocoa2 }}
                  onMouseEnter={(e) => ((e.currentTarget.style.color = accent))}
                  onMouseLeave={(e) => ((e.currentTarget.style.color = cocoa2))}
                >
                  <span className={isAr ? "font-medium" : ""}>
                    {t(item.label, lang)}
                  </span>
                  <span
                    className="pointer-events-none absolute inset-x-0 -bottom-1 mx-auto h-0.5 w-0 rounded-full transition-all duration-200 group-hover:w-full"
                    style={{ backgroundColor: accent }}
                  />
                </a>
              ))}
            </nav>

            {/* Right side */}
            <div className="flex items-center gap-2">
              {/* Language toggle – glass + chocolate */}
              <div
                className="relative inline-flex items-center overflow-hidden rounded-full border px-1.5 py-0.5 text-[11px] font-medium shadow-sm md:text-xs"
                dir="ltr"
                style={{
                  borderColor: "rgba(58,30,18,0.12)",
                  backgroundColor: "rgba(255,255,255,0.65)",
                }}
              >
                <span
                  className={`absolute inset-y-0.5 h-[calc(100%-4px)] w-1/2 rounded-full transition-all duration-200 ${
                    lang === "en" ? "left-0" : "left-1/2"
                  }`}
                  style={{
                    backgroundColor: accent,
                    boxShadow: "0 10px 22px rgba(226,138,58,0.30)",
                  }}
                />
                <button
                  type="button"
                  onClick={() => handleChangeLang("en")}
                  className={`relative z-10 px-3 py-1.5 transition-colors ${
                    lang === "en"
                      ? "text-white"
                      : "text-[#5b3324] hover:text-[#2d140b]"
                  }`}
                >
                  EN
                </button>
                <button
                  type="button"
                  onClick={() => handleChangeLang("ar")}
                  className={`relative z-10 px-3 py-1.5 transition-colors ${
                    lang === "ar"
                      ? "text-white"
                      : "text-[#5b3324] hover:text-[#2d140b]"
                  }`}
                >
                  ع
                </button>
              </div>

              {/* Mobile menu button */}
              <button
                type="button"
                className="flex h-9 w-9 items-center justify-center rounded-full border shadow-sm md:hidden"
                aria-label={lang === "en" ? "Toggle menu" : "فتح/إغلاق القائمة"}
                aria-expanded={isMenuOpen}
                onClick={toggleMenu}
                style={{
                  borderColor: "rgba(58,30,18,0.12)",
                  backgroundColor: "rgba(255,255,255,0.70)",
                  color: cocoa,
                }}
              >
                <div className="flex flex-col gap-0.5">
                  <span
                    className={`h-[2px] w-4 origin-center rounded-full transition-transform ${
                      isMenuOpen ? "translate-y-[3px] rotate-45" : ""
                    }`}
                    style={{ backgroundColor: cocoa }}
                  />
                  <span
                    className={`h-[2px] w-4 origin-center rounded-full transition-opacity ${
                      isMenuOpen ? "opacity-0" : "opacity-100"
                    }`}
                    style={{ backgroundColor: cocoa }}
                  />
                  <span
                    className={`h-[2px] w-4 origin-center rounded-full transition-transform ${
                      isMenuOpen ? "-translate-y-[3px] -rotate-45" : ""
                    }`}
                    style={{ backgroundColor: cocoa }}
                  />
                </div>
              </button>
            </div>
          </div>
        </div>
      </div>

      {/* Mobile dropdown menu */}
      {isMenuOpen && (
        <div
          className="fixed inset-0 z-40 bg-black/25 backdrop-blur-sm md:hidden"
          onClick={toggleMenu}
        >
          <div
            className="absolute inset-x-4 top-20"
            onClick={(e) => e.stopPropagation()}
          >
            <div
              className="origin-top rounded-3xl border p-4 shadow-2xl backdrop-blur-xl animate-[fadeDown_0.18s_ease-out]"
              style={{
                borderColor: "rgba(255,255,255,0.7)",
                backgroundColor: "rgba(255,255,255,0.88)",
              }}
            >
              <nav className="flex max-h-[60vh] flex-col gap-1 overflow-y-auto text-base font-medium">
                {navLinks.map((item) => (
                  <a
                    key={item.id}
                    href={`#${item.id}`}
                    onClick={handleNavClick}
                    className="flex items-center justify-between rounded-2xl px-4 py-3 hover:bg-black/5 active:bg-black/10"
                    style={{
                      direction: isAr ? "rtl" : "ltr",
                      color: cocoa,
                    }}
                  >
                    <span>{t(item.label, lang)}</span>
                    <span
                      className="h-1.5 w-1.5 rounded-full"
                      style={{ backgroundColor: accent, opacity: 0.28 }}
                    />
                  </a>
                ))}
              </nav>
            </div>
          </div>
        </div>
      )}
    </header>
  );
}
