// components/sections/PromosSection.tsx
"use client";

import { useState } from "react";
import { Lang } from "../../lib/siteConfig";

interface PromosSectionProps {
  lang: Lang;
}

function VolumeIcon({ muted }: { muted: boolean }) {
  return muted ? (
    <svg viewBox="0 0 24 24" aria-hidden="true" className="h-4 w-4">
      <path
        fill="currentColor"
        d="M11 5.5 7.6 8H5a2 2 0 0 0-2 2v4a2 2 0 0 0 2 2h2.6L11 18.5a1 1 0 0 0 1.6-.8V6.3a1 1 0 0 0-1.6-.8ZM19.3 8.7l-1.4 1.4L16.5 8.7a1 1 0 1 0-1.4 1.4l1.4 1.4-1.4 1.4a1 1 0 1 0 1.4 1.4l1.4-1.4 1.4 1.4a1 1 0 0 0 1.4-1.4l-1.4-1.4 1.4-1.4a1 1 0 0 0-1.4-1.4Z"
      />
    </svg>
  ) : (
    <svg viewBox="0 0 24 24" aria-hidden="true" className="h-4 w-4">
      <path
        fill="currentColor"
        d="M11 5.5 7.6 8H5a2 2 0 0 0-2 2v4a2 2 0 0 0 2 2h2.6L11 18.5a1 1 0 0 0 1.6-.8V6.3a1 1 0 0 0-1.6-.8Zm6.2 1.9a1 1 0 0 0-1.4 1.4A5 5 0 0 1 17 12a5 5 0 0 1-1.2 3.2 1 1 0 1 0 1.4 1.4A7 7 0 0 0 19 12a7 7 0 0 0-1.8-4.6Zm2.4-2.4a1 1 0 0 0-1.4 1.4A9 9 0 0 1 21 12a9 9 0 0 1-2.8 6.6 1 1 0 1 0 1.4 1.4A11 11 0 0 0 23 12a11 11 0 0 0-3.4-7Z"
      />
    </svg>
  );
}

function Reel({
  src,
  badgeText,
  wrapperClassName = "",
  frameClassName = "",
}: {
  src: string;
  badgeText: string;
  wrapperClassName?: string;
  frameClassName?: string;
}) {
  const [muted, setMuted] = useState(true);

  return (
    <div
      className={
        "group relative overflow-hidden rounded-[28px] border border-white/10 bg-black/20 " +
        "shadow-[0_22px_60px_rgba(0,0,0,0.35)] " +
        wrapperClassName
      }
    >
      {/* Frame */}
      <div className={"relative w-full overflow-hidden " + frameClassName}>
        <video
          className="absolute inset-0 h-full w-full object-cover"
          src={src}
          autoPlay
          loop
          playsInline
          muted={muted}
          preload="metadata"
        />

        {/* overlay (reels vibe) */}
        <div className="pointer-events-none absolute inset-0 bg-gradient-to-t from-black/60 via-black/18 to-black/0" />

        {/* badge top */}
        <div className="pointer-events-none absolute left-3 top-3 rounded-full bg-white/90 px-3 py-1 text-[11px] font-semibold uppercase tracking-wide text-slate-900">
          {badgeText}
        </div>

        {/* controls bottom */}
        <div className="absolute bottom-3 left-3 right-3 flex items-center justify-between gap-2">
          <div className="pointer-events-none inline-flex items-center gap-2 rounded-full bg-white/10 px-3 py-2 text-[11px] font-semibold text-white backdrop-blur-md">
            <span className="h-1.5 w-1.5 rounded-full bg-white/70" />
            <span>Reel</span>
          </div>

          <button
            type="button"
            onClick={() => setMuted((m) => !m)}
            className="inline-flex items-center gap-2 rounded-full bg-black/55 px-3 py-2 text-[11px] font-semibold text-white backdrop-blur-md transition hover:bg-black/65 focus:outline-none focus:ring-2 focus:ring-white/60"
            aria-label={muted ? "Unmute" : "Mute"}
          >
            <VolumeIcon muted={muted} />
            <span className="hidden sm:inline">{muted ? "Sound off" : "Sound on"}</span>
          </button>
        </div>
      </div>
    </div>
  );
}

export function PromosSection({ lang }: PromosSectionProps) {
  const reels = [
    { src: "/promos/reel-1.mp4", badge: { en: "JOORY", ar: "جوري" } },
    { src: "/promos/reel-2.mp4", badge: { en: "COFFEE", ar: "قهوة" } },
    { src: "/promos/reel-3.mp4", badge: { en: "DESSERT", ar: "حلى" } },
  ];

  const badgeText = (b: { en: string; ar: string }) => (lang === "ar" ? b.ar : b.en);

  return (
    <section id="promos" className="bg-[#02252b] py-12 md:py-16">
      <div className="mx-auto max-w-7xl px-4">
        {/* ✅ PHONE: one-by-one, BIG reels (near full screen height) */}
        <div className="md:hidden space-y-6">
          {reels.map((r, i) => (
            <div key={i} className="mx-auto w-full max-w-[420px]">
              <Reel
                src={r.src}
                badgeText={badgeText(r.badge)}
                // 78vh gives a perfect reels feel, not too huge, not too small
                frameClassName="h-[78vh]"
              />
            </div>
          ))}
        </div>

        {/* ✅ DESKTOP: bigger layout (2 cols then 3 cols) */}
        <div className="hidden md:grid md:grid-cols-2 md:gap-7 lg:grid-cols-3 lg:gap-8">
          {reels.map((r, i) => (
            <Reel
              key={i}
              src={r.src}
              badgeText={badgeText(r.badge)}
              // keep reels aspect on desktop, makes them tall & premium
              frameClassName="aspect-[9/16]"
            />
          ))}
        </div>
      </div>
    </section>
  );
}
